@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
:root {
  --primary-color: #4e3679;
}

body {
  background-color: #f1f1f1 !important;
  padding-bottom: 50px !important;
  font-family: "Noto Sans" !important;
}

.image {
  width: 60px;
  height: 60px;
  border-radius: 20px;
  overflow: hidden;
  position: absolute;
  top: -30px;
  left: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}

.company-logo {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: white;
  padding: 5px;
  text-align: center;
}

.job-upper {
  display: flex;
}
.job {
  width: 350px;
  margin-top: 50px;
  height: 250px;
  padding: 10px 25px;
  position: relative;
  border-radius: 7px;
  background-color: white;
  cursor: pointer;
  transition: 0.2s;
}

.job:hover {
  transform: scale(1.02);
}

.jobs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin-bottom: 50px;
}

.secondary-text {
  color: rgb(99, 99, 99);
}

.primary-text {
  font-size: 17px;
  font-weight: 600 !important;
}

.job-footer {
  display: flex;
  position: absolute;
  bottom: 15px;
  margin-left: 5px;
}
.location-text {
  font-weight: bold;
  color: #4e3679;
  color: var(--primary-color);
  font-size: 14px;
}
.primary-button {
  display: block;
  margin: 0 auto;
  background-color: #4e3679;
  background-color: var(--primary-color);
  border: none;
  padding: 12px 25px;
  color: white;
  border-radius: 5px;
  font-family: "Noto Sans";
  font-weight: bold;
  outline: none;
  transition: 0.2s all ease-out;
}

.primary-button:hover {
  background-color: #4e3679d8;
}

.primary-button:active {
  transform: scale(0.95);
  background-color: #322250;
}

.primary-button:focus {
  outline: none;
}

.loading {
  display: flex;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background-color: rgba(255, 255, 255, 0.1);
  justify-content: center;
  align-items: center;
}

.scroll-to-top {
  display: flex;
  width: 50px;
  height: 50px;
  position: fixed;
  top: 90%;
  left: 90%;
  border-radius: 25px;
  border: 4px solid #4e3679;
  border: 4px solid var(--primary-color);
  justify-content: center;
  align-items: center;
  outline: none;
  background: none;
  z-index: 1;
}

.scroll-to-top:focus {
  outline: none;
}

.scroll-enter {
  display: none;
  opacity: 0;
}
.scroll-enter-active {
  display: block;
  opacity: 0;
}
.scroll-enter-done {
  opacity: 1;
  transition: opacity 200ms;
}
.scroll-exit {
  opacity: 1;
}
.scroll-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}
.scroll-exit-done {
  opacity: 0;
  display: none;
}

.navbar {
  display: flex;
  justify-content: space-around;
}

.nav-background {
  padding: 20px;
  padding-top: 75px;
  height: 200px;
  background-color: #4e3679;
  background-color: var(--primary-color);
  border-bottom-left-radius: 125px;
  margin-bottom: 150px;
}

.filters {
  width: 100%;
  height: 80px;
  background-color: white;
  border-radius: 5px;
  z-index: 2;
  overflow: hidden;
  position: relative;
  margin-top: -100px;
  top: -90px;
}

.search {
  width: 100%;
  height: 100%;
  border-right-width: 1px;
  border-right-style: solid;
  border-right-color: #8d8d8d;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search input,
.filter input {
  width: 100%;
  height: 90%;
  border: 0;
  padding-left: 20px;
  outline: none;
}

.filter {
  width: 100%;
  height: 100%;
  z-index: 2;
  border-right-width: 1px;
  border-right-style: solid;
  border-right-color: #8d8d8d;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-button {
  display: flex;
  justify-content: center;
  align-items: center;
}

#full-time {
  width: 20px;
  height: 20px;
  background: gray;
  border: none;
}

#full-time-label {
  font-weight: bold;
  margin-top: 7px;
  margin-left: 5px;
}

.dev-logo {
  color: white !important;
  text-decoration: none !important;
}

.custom-container {
  padding-left: 5% !important;
  padding-right: 5% !important;
}

.upper {
  display: flex;
  justify-self: center;
  height: 180px;
  align-items: center;
  background-color: white;
  position: relative;
  margin: auto;
  margin-top: -100px;
  top: -90px;
  border-radius: 5px;
  overflow: hidden;
}
.details-company-logo {
  width: 180px;
  height: 180px;
  padding: 20px;
  object-fit: contain;
  background: white;
}

.company-details {
  margin-left: 50px;
}

.company-site-button {
  margin-right: 50px;
}

.job-body {
  background-color: white;
  margin-top: -50px;
  padding: 50px;
  border-radius: 5px;
}

a {
  text-decoration: none !important;
}

